<script>
  export default {
    props: ['sessionList', 'user', 'userList'],
    computed: {
      sessionUser () {
        let users = this.userList.filter(item => item.id === this.session.userId)
        return users[0]
      }
    },
    filters: {
      avatar (message) {
        let user = message.self ? this.user : this.sessionUser
        return user && user.img
      },
      time (date) {
        if (typeof(date) === 'string') {
          date = new Date(date)
        }
        return date.getHours() + ':' + date.getMinutes()
      }
    },
    directives: {
      'scroll-bottom' () {
        Vue.nextTick(() => {
          this.el.scrollTop = this.el.scrollHeight - this.el.clientHeight
        })
      }
    }
  }
</script>

<template>
  <div class="m-message" v-scroll-bottom="session.messages">
    <ul>
      <li v-for="message in session.messages">
        <p class="time">
          <span>{{message.date | time}}</span>
        </p>
        <div class="main" :class="{self: message.self}">
          <img class="avatar" width="30" height="30" :src="message | avatar"  />
          <div class="text">
            {{message.text}}
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<style lang="less">
.m-message {
  padding: 10px 15px;
  overflow-y: scroll;
  li {
    margin-bottom: 15px;
  }
  .time {
    margin: 7px 0;
    text-align: center;
    span {
      display: inline-block;
      padding: 0 18px;
      font-size: 12px;
      color: #fff;
      background-color: #dcdcdc;
      border-radius: 2px;
    }
  }
}
</style>



